<template>
  <div class="chart-container">
    <div
      class="chart"
      :style="setChartContainer"
    >
    </div>
  </div>
</template>

<script>
import echarts from "echarts/echarts";
import "echarts/chart/wordCloud";
export default {
  name: 'vue-echart-wordcloud',
  mounted () {
    this.editChartData();
    console.log(this.chartContent);
    this.drawChart();
  },
  methods: {
    drawChart () {
      // echarts
      let myChart = echarts.init(document.querySelector('.chart'));
      // 
      let charCloudOption = {
        series: [{
          // 
          name: 'word cloud',
          // 
          type: 'wordCloud',
          // 
          size: ['100%', '100%'],
          textRotation: [0, 0],
          textPadding: 0,
          autoSize: {
            enable: true,
            minSize: 12
          },
          data: this.chartContent
        }]
      };
      myChart.setOption(charCloudOption);
    },
    createItemStyle (color) {
      return {
        normal: {
          color: color
        }
      }
    },
    editChartData () {
      this.chartContent.forEach(item => {
        if (item.itemStyle) {
          let color = item.itemStyle;
          item.itemStyle = this.createItemStyle(color);
        }
      })
    }
  },
  computed: {
    setChartContainer () {
      return `width: ${this.chartWidth}px; height: ${this.chartHeight}px`;
    }
  },
  props: {
    // 
    chartWidth: {
      type: Number,
      required: true
    },
    // 
    chartHeight: {
      type: Number,
      required: true
    },
    // 
    chartContent: {
      type: Array,
      required: true
    }
  }
}
</script>

<style lang="scss" scoped>
.chart-container {
  background-color: transparent;
}
</style>



